.popup__form-profile-image {
  background-color: #fff;
  border-radius: 15px;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0;
  padding: 34px;
}


@media (max-width: 630px) {
  .popup__form-profile-image {
    max-width: 282px;
  }
}